import React from 'react';
import logo from '../../img/web-logo.png';
import location from '../../img/location-header.png';
import avatar from '../../img/avatar.png';

function NavbarHome() {
    return (
        <div className="headerContent">
            <nav className="navbar navbar-expand-sm navbar-light">
                <a className="navbar-brand" href="#">
                    <img src={logo} alt="tix-logo" />
                </a>
                <button className="navbar-toggler d-lg-none" type="button" data-toggle="collapse" data-target="#collapsibleNavId" aria-controls="collapsibleNavId" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon" />
                </button>
                <ul className="navbar-nav">
                    <li className="nav-item active">
                        <a className="nav-link" href="#">Lch Chiu <span className="sr-only">(current)</span></a>
                    </li>
                    <li className="nav-item">
                        <a className="nav-link" href="#">Cm Rp</a>
                    </li>
                    <li className="nav-item">
                        <a className="nav-link" href="#">Tin Tc</a>
                    </li>
                    <li className="nav-item">
                        <a className="nav-link" href="#">ng Dng</a>
                    </li>
                </ul>
                <div className="logIn">
                    <ul className="navbar-nav mr-auto mt-2 mt-lg-0">
                        <li className="signIn">
                            <img src={avatar} alt="avatar" />
                            <button>ng Nhp</button>
                        </li>
                        <li className="location-list">
                            <img src={location} alt="location-img" />
                            <select className="location dropdown">
                                <option value="1">H Ch Minh</option>
                                <option value="2">H Ni</option>
                                <option value="3"> Nng</option>
                                <option value="4">Hi Phng</option>
                                <option value="5">Bin Ho</option>
                                <option value="6">Nha Trang</option>
                                <option value="7">Bnh Dng</option>
                                <option value="8">Phan Thit</option>
                                <option value="9">H Long</option>
                                <option value="10">Cn Th</option>
                                <option value="11">Vng Tu</option>
                                <option value="12">Hu</option>
                                <option value="13">Long Xuyn</option>
                                <option value="14">Thi Nguyn</option>
                                <option value="15">Bun M Thut</option>
                                <option value="16">Bc Giang</option>
                                <option value="17">Bn Tre</option>
                                <option value="18">Ninh Bnh</option>
                                <option value="19">Thi Bnh</option>
                                <option value="20">Vinh</option>
                                <option value="21">Bo Lc</option>
                                <option value="22"> Lt</option>
                            </select>
                        </li>
                    </ul>
                </div>
            </nav>
        </div>
    )
}

export default NavbarHome;
